import React from 'react';
import ItemSlides from './ItemSlides';
import ItemList from './ItemList';
import DisplayEditor from './DisplayEditor';
import Backgrounds from './Backgrounds';
import AllItems from './AllItems';

export default class FullView extends React.Component{

  render() {

    let {wordIndex, setItemIndex, setWordIndex, isLoggedIn, swapItemInList, swapWords,
      addItem, itemList, deleteItemFromList, item, updateItem, backgrounds, itemIndex,
      setItemBackground, handleFileChange, allItems, deleteItem, addItemToList} = this.props;

    return (
      <div style={{width:'99%', marginLeft:'1vmax'}}>
        <div style={{display:'flex', fontSize:'calc(10px + 1vmax)'}}>
          <div style={{width:'17.5%', overflowX:'hidden'}}>
            <ItemList setItemIndex={setItemIndex} setWordIndex={setWordIndex}
              isLoggedIn={isLoggedIn} addItem={addItem} itemList={itemList}
              deleteItemFromList={deleteItemFromList} backgrounds={backgrounds}
              updateItem={updateItem} swapItemInList={swapItemInList} itemIndex={itemIndex}
              />
          </div>
          <div style={{width:'82.5%', display:'block'}}>
            <div style={{display:'flex', height:"57%", maxHeight:"31vw"}}>
              <div style={{width:'44.5vw', marginLeft:'0.5vmax'}}>
                <DisplayEditor wordIndex={wordIndex} backgrounds={backgrounds}
                  isLoggedIn={isLoggedIn} item={item} updateItem={updateItem}/>
              </div>
              <div style={{width:'54%', marginLeft:'1vmax'}}>
                <Backgrounds backgrounds={backgrounds} handleFileChange={handleFileChange}
                  setItemBackground={setItemBackground} isLoggedIn={isLoggedIn}
                  />
              </div>
            </div>
            <div style={{display: 'flex', height:'43%', fontSize:'calc(10px + 1vmax)'}}>
                <div style={{width:'56%', marginLeft:'0.5vmax'}}>
                  <ItemSlides setWordIndex={setWordIndex} wordIndex={wordIndex}
                    isLoggedIn={isLoggedIn} item={item} updateItem={updateItem}
                    backgrounds={backgrounds} swapWords={swapWords}
                    />
                </div>
                <div style={{width:'40%', marginLeft:'0.5vmax'}}>
                  <AllItems isLoggedIn={isLoggedIn} allItems={allItems}
                    deleteItem={deleteItem} addItemToList={addItemToList}
                    />
                </div>
            </div>
          </div>

        </div>
      </div>
    )
  }

}



