import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import Iframe from 'react-iframe'

export default class Home extends Component {

  constructor(){
    super();
    this.state = {
      url: ''
    }

    this.test = this.test.bind(this);
    this.start = this.start.bind(this);
  }

  start(){
    let url = 'https://www.biblegateway.com/passage/?search=Jeremiah+4%3A4-10&version=NKJV'
    this.setState({url:url})
  }

  test(){
    var doc = document.getElementById('iframe');
    var bod = doc.body;
    console.log(doc)

  }

  render(){

    return(
      <div style={{height:'95vh'}}>
        <nav style={{fontSize: "calc(10px + 0.35vmax)"}}>
          <h1 style={{padding: '1%'}}>Welcome to Portable Media</h1>
          <ul style={{paddingLeft: '1%'}}>
           {/* Link components are used for linking to other views */}
            <li><Link to="/fullview">Full View</Link></li>
            <li><Link to="/mobile">Mobile View</Link></li>
            <li><Link target="_blank" to="/presentation">Presentation</Link></li>
            <li><button style={{fontSize: "calc(10px + 0.35vmax)"}} onClick={this.start}>Start Bible</button></li>
            <li><button style={{fontSize: "calc(10px + 0.35vmax)"}} onClick={this.test}>Test</button></li>
            {!this.props.isLoggedIn && <li><button style={{fontSize: "calc(10px + 0.35vmax)"}}><Link to="/login">Login</Link></button></li>}
            {this.props.isLoggedIn && <li><button style={{fontSize: "calc(10px + 0.35vmax)"}} onClick={this.props.logout}>Logout</button></li>}
          </ul>
         </nav>
         <Iframe url={this.state.url}
            width="450px"
            height="450px"
            id="iframe"
            className="myClassname"
            display="initial"
            position="relative"/>
      </div>
    )
  }

}



