import React from 'react';
import blank from './assets/blank.png';

class DisplayEditor extends React.Component{

  constructor(){
    super();
    this.handleTextChange = this.handleTextChange.bind(this);
  }


  handleTextChange(event) {
      let {wordIndex, item} = this.props;
      if(item.words){
        if(event.target.value === "")
          item.words[wordIndex] = " ";
        else
          item.words[wordIndex] = event.target.value;
        this.props.updateItem(item);
      }
    }

  render() {

    let {wordIndex, isLoggedIn, item, backgrounds} = this.props;
    let words = item.words;
    let text = "", background = blank;
    let style = {
      textAlign: 'center',
      background: 'transparent',
      border: 0,
      resize:'none',
      height: '50vh',
      maxHeight:"30vw",
      width: '44vw',
      fontFamily: "Verdana",
      paddingTop: "2%",
    }

    if(words){
       text = item.words[wordIndex];
         style.color = item.style.color;
      if(wordIndex === 0)
        style.fontSize = item.nameSize + "vmax";
      else
         style.fontSize = item.style.fontSize*.95 + "vmax";

    }
    if(backgrounds){
      if(backgrounds.some(e => e.name === item.background)){
          background = backgrounds.find(e => e.name === item.background).data;
        }
    }


    return (
        <div style={{backgroundImage: 'url('+background+')',
          width: "100%", height: "100%", backgroundSize: '100% 100%'}}>
        {isLoggedIn && <div>
          <textarea style={style} value={text} onChange={this.handleTextChange}/>
          </div>}

        {!isLoggedIn && <div style ={style}> {text} </div>}
        </div>
    )
  }

}
export default DisplayEditor;



