import PouchDB from 'pouchdb';

// var db = new PouchDB('https://b0e964d2-9f5a-4e41-b2c3-4736c87d2782-bluemix:c7097fe2baba37a6e1cebb4412b8da38a3f9800254307eeb0b7c1779c9b95d30@b0e964d2-9f5a-4e41-b2c3-4736c87d2782-bluemix.cloudant.com/portable-media');
// var db = new PouchDB('http://192.168.1.84:5986/portable-media');
var db = new PouchDB('http://192.168.1.84:5986/portable-media-test');

export default function DBSetup(){


  let SLcount = 5;
  for (var i = 1; i <= SLcount; ++i){
    SLists(i);
  }

  function SLists(i){
    db.get("Item List "+i).catch(function (err) {
      var SL = {
      "_id": "Item List "+i,
      "items" : []
      };
       db.put(SL)
    })
  }

  db.get("allItems").catch(function (err) {
    var SL = {
    "_id": "allItems",
    "items" : []
    };
     db.put(SL)
  })

  db.get("ItemLists").catch(function (err) {
    var SL = {
    "_id": "ItemLists",
    "itemLists" : [
      "Item List 1",
      "Item List 2",
      "Item List 3",
      "Item List 4",
      "Item List 5"
    ]
    };
     db.put(SL)
  })

  db.get("currentWords").catch(function(err){
      let upload = {
        _id: "currentWords",
        info: ""
      }
      db.put(upload);
  })

  db.get("currentStyle").catch(function(doc){
      let upload = {
        _id: "currentStyle",
        info: {
          color: '',
          fontSize: ''
        }
      }
      db.put(upload);
  })

  db.get("currentBackground").catch(function(doc){
      let upload = {
        _id: "currentBackground",
        info: ""
      }
      db.put(upload);
    })

    db.get("currentInfo").catch(function(doc){
        let upload = {
          _id: "currentInfo",
          info: {
            words:"",
            background:"",
            style:{
              color:'',
              fontSize:''
            }
          }
        }
        db.put(upload);
      })

  db.get("images").catch(function(doc){
      let upload = {
        _id: "images",
        names: []
      }
      db.put(upload);
    })

}



