import React from 'react';
import blank from './assets/blank.png';

export default class SlideInList extends React.Component{


  render(){
  let {words, backgrounds, background, color, fontSize} = this.props;
  let img = blank;
  let style = {
    textAlign: 'center',
    background: 'transparent',
    border: 0,
    resize:'none',
    height: '100%',
    width: '100%',
    fontFamily: "Verdana",
    paddingTop: "2%",
    fontSize: fontSize*0.265+"vmax",
    whiteSpace:'pre-wrap',
  }

  if (color)
    style.color = color

    if(backgrounds){
      if(backgrounds.some(e => e.name === background))
        img = backgrounds.find(e => e.name === background).data;
    }


    return (
        <div style={{backgroundImage: 'url('+img+')',
          width: "100%", height: "100%", backgroundSize: '100% 100%'}}>
          <div style={style}>
            {words}
          </div>
        </div>
    )
  }

}



