import React from 'react';
import blank from './assets/blank.png';

class Presentation extends React.Component{


  render() {

    let {backgrounds, background, style} = this.props;
    let img = blank
    let styleFull = {
      textAlign: 'center',
      background: 'transparent',
      border: 0,
      resize:'none',
      height: '100%',
      width: '100%',
      whiteSpace:'pre-wrap',
      color: style.color,
      fontSize: style.fontSize*2.2+"vw",
      fontFamily: "Verdana",
      paddingTop: "2%"
    }

    if(backgrounds.some(e => e.name === background)){
      img = backgrounds.find(e => e.name === background).data;
    }

    return (
      <div style={{verticalAlign: 'middle'}}>
        <div style={{backgroundImage: 'url('+img+')',
        width: '100vw', height: '70vw', backgroundSize: '100% 100%', position:'fixed'}}>
          <div style={styleFull}>
              {this.props.text}
          </div>
        </div>
      </div>
    )
  }

}
export default Presentation;



