import React from 'react';
import deleteX from './assets/deleteX.png';
import edit from './assets/edit.png';
import up from './assets/up-arrow.png';
import down from './assets/down-arrow.png';
import CreateName from './CreateName';
import ItemInList from './ItemInList';
import PouchDB from 'pouchdb'

// var db = new PouchDB('https://b0e964d2-9f5a-4e41-b2c3-4736c87d2782-bluemix:c7097fe2baba37a6e1cebb4412b8da38a3f9800254307eeb0b7c1779c9b95d30@b0e964d2-9f5a-4e41-b2c3-4736c87d2782-bluemix.cloudant.com/portable-media');
// var db = new PouchDB('http://192.168.1.84:5986/portable-media');
var db = new PouchDB('http://192.168.1.84:5986/portable-media-test');

export default class ItemList extends React.Component{

  constructor(){
      super();

      this.state = {
        CNOpen: false,
        message: "",
        name: "",
        index: 0,
        isSong: false,
        isImage: false,
      }

      this.addItem = this.addItem.bind(this);
      this.editName = this.editName.bind(this);
      this.editItem = this.editItem.bind(this);
      this.createItem = this.createItem.bind(this);
      this.cancelName = this.cancelName.bind(this);
      this.nameAvailable = this.nameAvailable.bind(this);
      this.itemUp = this.itemUp.bind(this);
      this.itemDown = this.itemDown.bind(this);
    }

  addItem(name){

    let {isSong, isImage} = this.state;

    let firstSlide = "";
    let type;

    if(isSong)
      type = 'song'
    else if(isImage)
      type = 'image'

    if(isSong){
      if(name.length < 11)
        firstSlide = "\n"+ name;
      else{
        firstSlide = "\n"+ name;
      }
    }

    let nameSize = 5;


    let {backgrounds} = this.props;
    let background = "";
    if (backgrounds[0])
      background = backgrounds[0].name

    let item = {
                "_id": name,
                "name": name,
                "background": background,
                "words": [
                  firstSlide
                ],
                "nameSize": nameSize
                ,
                "style":{
                  color: 'rgba(255, 255, 255, 1)',
                  fontSize: 3
                },
                "type": type
              }
      this.props.addItem(item);
    }

    createItem(type){
      this.setState({CNOpen: true})
      if(type === 'song'){
        this.setState({
          isSong: true,
          isImage: false,
        })
      }
      if(type === 'image'){
        this.setState({
          isSong: false,
          isImage: true,
        })
      }
    }

    editItem(index, name){
      this.setState({
        ENOpen: true,
        name: name,
        index: index
      })
    }

    nameAvailable(name){
      var that = this;
      db.get(name).then(function(){
        that.setState({message: "Name Not Available"})
      }).catch(function(){
        that.setState({
          CNOpen: false,
          ENOpen: false
        })
        that.addItem(name)
      })
    }

    editName(name){
      let id = this.props.itemList[this.state.index]._id;
      this.props.setItemIndex(this.state.index)
      let that = this;

      db.get(id).then(function(doc){
        let item = doc;
        item.name = name;
        item.words[0] = name;
        that.props.updateItem(item);
      })

      this.setState({ENOpen: false})
    }

    cancelName(){
      this.setState({
        CNOpen: false,
        ENOpen: false,
        message: ""
      })
    }

    itemUp(index){
      this.props.swapItemInList(index, index-1)
    }

    itemDown(index){
      this.props.swapItemInList(index, index+1)
    }


  render(){
  let {itemList, isLoggedIn, backgrounds, itemIndex} = this.props;
  let {CNOpen, ENOpen, name} = this.state;

  let list = [];
  for(var i = 0; i < itemList.length; ++i){
    list.push(itemList[i]);
  }

  let SL = list.map((element, index) => {
  return(
    <div style={{display:'flex'}} key={index} id={"Item"+index}>
      <div style={(index === itemIndex) ? {border:'0.25vmax', borderColor: '#4286f4', borderStyle:'solid',
         width:"13.5vmax", height:"7.65vmax", marginTop:"3%"}
          : {border:'0.25vmax', borderColor: '#d9e3f4', borderStyle:'solid',
         width:"13.5vmax", height:"7.65vmax", marginTop:"3%"}}
        onClick={() => (this.props.setItemIndex(index))}
        >
        <ItemInList name={element.name} background={element.background} type={element.type}
          nameColor = {element.nameColor} backgrounds={backgrounds}
          />
      </div>
        {isLoggedIn && <div style={{padding:"3%"}}>
          <img style={{display:'block', width:'1.5vmax', height:'1.5vmax'}}
             onClick={() => (this.props.deleteItemFromList(index))}
             alt="delete" src={deleteX}
             />
           <img style={{display:'block', paddingTop:"10%", width:'1.5vmax', height:'1.5vmax'}}
            onClick={() => (this.editItem(index, element.name))}
            alt="edit" src={edit}
            />
          {(index !== 0) && <img style={{display:'block', paddingTop:"10%", width:'1.5vmax', height:'1.5vmax'}}
             onClick={() => (this.itemUp(index))}
             alt="up" src={up}
             />
          }
          {(index !== list.length-1) && <img style={{display:'block', paddingTop:"10%", width:'1.5vmax', height:'1.5vmax'}}
              onClick={() => (this.itemDown(index))}
              alt="down" src={down}
              />
          }
        </div>}
    </div>
  )
})
    return (
      <div>
        {isLoggedIn && <button style={{fontSize: "calc(8px + 0.4vmax)", marginRight: 'calc(5px + 0.5vmax)'}} onClick={() => this.createItem('song')}>Add Song</button>}
        {isLoggedIn && <button style={{fontSize: "calc(8px + 0.4vmax)"}} onClick={() => this.createItem('image')}>Add Image</button>}
        {CNOpen && <CreateName submitName={this.nameAvailable} name="New Item"
        cancelName={this.cancelName} message={this.state.message}
        />}
        {ENOpen && <CreateName submitName={this.editName} name={name}
        cancelName={this.cancelName} message={this.state.message}
        />}
        <div style={{ height:'87.5vh', overflowY: 'scroll'}}>{SL}</div>

      </div>
    )
  }

}



