import React, { Component }  from 'react';

export default class Login extends Component {

  constructor() {
    super();

    this.state = {
      username: "",
      password: "",
      wrongCred: false
    }
    this.login = this.login.bind(this);
    this.usernameChange = this.usernameChange.bind(this);
    this.passwordChange = this.passwordChange.bind(this);
  }

  usernameChange(event) {
    this.setState({username: event.target.value});
  }

  passwordChange(event) {
    this.setState({password: event.target.value});
  }

  login(event) {

    event.preventDefault();
    if(this.state.username === "Panda" && this.state.password === "Express"){
      this.props.login();
      this.setState({wrongCred: false})
      this.props.history.push("/fullview");
    }
    else{
      this.setState({wrongCred: true})
    }

  }

  render() {

    return (
      <div>
          <form onSubmit={this.login}>
            <ul className="">
              <li className="">
                <label> Username: </label>
                <input className="" type="text" value={this.state.username} onChange={this.usernameChange} />
              </li>
              <li className="">
                <label> Password:  </label>
                <input className="" type="password" value={this.state.password} onChange={this.passwordChange} />
              </li>
              <li >
                <input className="" type="submit" value="Submit" />
              </li>
              {this.state.wrongCred &&
                <p>The Username and Password combination do not match</p>
              }
            </ul>
          </form>
      </div>
    )
  }
}



