import React, {Component} from 'react';

export default class LyricsBox extends Component{

  constructor(){
    super();
    this.state = {
      text: ""
    }
    this.submitText = this.submitText.bind(this);
    this.textChange = this.textChange.bind(this);

  }

  submitText(){
    this.props.autoFormatLyrics(this.state.text);
  }

  textChange(event){
    this.setState({text: event.target.value})
  }

  render(){
    return(
      <div style={{position:'fixed', zIndex:2, right:'36%', top:'10%',
        width:'25vw', height: '65vh', backgroundColor:"#EEE", padding:'1%'}}>
        <textarea style={{width:'100%', height:'90%', whiteSpace:'pre-wrap'}} value={this.state.text} onChange={this.textChange}/>
        <div style={{display:'flex'}}>
          <button onClick={this.submitText}>Auto Format Lyrics</button>
          <button onClick={this.props.cancel}>Cancel</button>
        </div>

        </div>
    )
  }

}



