import React, { Component } from 'react';
import { Link, Route, Switch} from 'react-router-dom';
import './App.css';
import Presentation from './Presentation';
import FullView from './FullView';
import FormatEditor from './FormatEditor';
import Login from './Login';
import PouchDB from 'pouchdb';
import DBSetup from './DBSetup';
import Home from './Home';
import MobileView from './MobileView'
import { blobToDataURL } from 'blob-util'

// var db = new PouchDB('https://b0e964d2-9f5a-4e41-b2c3-4736c87d2782-bluemix:c7097fe2baba37a6e1cebb4412b8da38a3f9800254307eeb0b7c1779c9b95d30@b0e964d2-9f5a-4e41-b2c3-4736c87d2782-bluemix.cloudant.com/portable-media');
// var db = new PouchDB('http://192.168.1.84:5986/portable-media');
var db = new PouchDB('http://192.168.1.84:5986/portable-media-test');

//Handle First Run Database Setup
DBSetup();

/* App component */
class App extends Component {

  constructor(){
    super();

    this.state = {
      isLoggedIn: true,
      wordIndex: 0,
      itemIndex: 0,
      item: {},
      itemList: [],
      itemLists: [],
      backgrounds: [],
      selectedItemList: "",
      needsUpdate: false,
      currentInfo : {
        words:"",
        background:"",
        style:{
          color:'',
          fontSize:''
        }
      },
      allItems:[],
      url: ""
    }

    this.login = this.login.bind(this);
    this.logout = this.logout.bind(this);
    this.setWordIndex = this.setWordIndex.bind(this);
    this.setItemIndex = this.setItemIndex.bind(this);
    this.updateItem = this.updateItem.bind(this);
    this.addItem = this.addItem.bind(this);
    this.addItemToList = this.addItemToList.bind(this);
    this.deleteItemFromList = this.deleteItemFromList.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.handleFileChange = this.handleFileChange.bind(this);
    this.uploadFile = this.uploadFile.bind(this);
    this.update = this.update.bind(this);
    this.updateFormat = this.updateFormat.bind(this);
    this.setItemBackground = this.setItemBackground.bind(this);
    this.selectItemList = this.selectItemList.bind(this);
    this.test = this.test.bind(this);
    this.swapItemInList = this.swapItemInList.bind(this);
    this.swapWords = this.swapWords.bind(this);
  }



  updateFormat({
    c = this.state.item.style.color,
    fontSize = this.state.item.style.fontSize,
    nameSize = this.state.item.nameSize
    } = {}){

    let {item, itemList, itemIndex, allItems} = this.state;
    let color;

    //if it recieves a value it is formatted as {r: r, g: g, b: b, a: a}
    //otherwise the saved value is formatted as rgba(r, g, b, a)
    if(typeof c === 'string')
      color = c;
    else
      color = 'rgba('+c.r+' , ' +c.g+' , '+c.b+' , '+c.a+')';

    item.nameSize = nameSize;
    item.style = {
      color: color,
      fontSize: fontSize
    }

    //update color of item in current list
    itemList[itemIndex].nameColor = color;

    //update color of item in full list
    let index = allItems.findIndex(e => e._id === item._id)
    allItems[index].nameColor = color;

    //update state, ready to update
    this.setState({
      item: item,
      itemList: itemList,
      needsUpdate: true
    });
  }

  update(){
     let {item, itemIndex, needsUpdate, selectedItemList, allItems} = this.state;
     //Save item components and update itemlists
     let index = allItems.findIndex(e => e._id === item._id)

     if(needsUpdate){
        db.get(item._id).then(function (doc) {
         doc.name = item.name;
         doc.words = item.words;
         doc.style = item.style;
         doc.nameSize = item.nameSize;
         return db.put(doc);
       })

       db.get(selectedItemList).then(function (doc) {
         doc.items[itemIndex].name = item.name;
         doc.items[itemIndex].background = item.background;
         doc.items[itemIndex].nameColor = item.style.color;
         return db.put(doc)
       })

       db.get('allItems').then(function (doc) {
           doc.items[index].name = item.name
           doc.items[index].background = item.background;
           doc.items[index].nameColor = item.style.color;
           db.put(doc);
       })

       this.setState({needsUpdate: false});
     }
  }

  componentDidMount(){
    let that = this;
    let {backgrounds} = this.state;
    //default to first Item list
    db.get('Item List 1').then(function (doc) {
      that.setState({itemList: doc.items}); //actual list of items
    });
    db.get('ItemLists').then(function (doc) {
      that.setState({
        itemLists: doc.itemLists, // list of item list names
        selectedItemList: doc.itemLists[0] //name of Item list
      });
    });
    db.get('allItems').then(function (doc) {
      that.setState({allItems: doc.items}); //every single Item
    });
    db.get('images').then(function(doc){
      let i = 0;
      let imgLoader = setInterval(function(){
        if(i >= doc.names.length){
          clearInterval(imgLoader);
          return;
        }
        db.getAttachment(doc.names[i], doc.names[i])
        .then(function(img){
            blobToDataURL(img).then(function(url){
              let imgdata = {
                "name": doc.names[i],
                "data": url
              }
              backgrounds.push(imgdata);
              that.setState({backgrounds: backgrounds})
            })
          })
          i+=1;
      },200)
    });

    //load first Item
    setTimeout(function() {
      if(this.state.allItems.length > 0)
        this.setItemIndex(0)
    }.bind(this), 1000);
    setInterval(this.update, 1000); //auto save to database every second if update has occurred
    //setInterval(this.presentationUpdate, 100); //update presentation variables
    db.changes({
      since: 'now',
      live: true,
      include_docs: true
    }).on('change', function(change) {
     if(change.id === "currentInfo"){
       that.setState({currentInfo: change.doc.info})
      }
    })

  }

  setItemBackground(background){
    let {item, isLoggedIn, itemIndex, itemList, allItems} = this.state;
    let index = allItems.findIndex(e => e.name === item.name)
    let that = this;

    if(!isLoggedIn)
      return;

    //update Item background in all places
    db.get(item._id).then(function(doc){
      doc.background = background;
      item.background = background;
      itemList[itemIndex].background = background;
      allItems[index].background = background;
      that.setState({
        item: item,
        needsUpdate: true,
        itemList: itemList,
        allItems: allItems
      })
      db.put(doc);
    })
  }



  handleFileChange(files: FileList){

    let i = 0;
    let ready = true;
    let nameUpload = setInterval(function(){
      if(i >= files.length){
        clearInterval(nameUpload)
        return;
      }

      var name = files[i].name;
      if(ready){

        ready = false;
        db.get("images").then(function(doc){
           if(!doc.names.includes(name)){
               doc.names.push(name);
           }
          return db.put(doc)
        }).then(function(){
          ready = true;
          i+=1;
        });
      }

    }, 500)
    for (let i = 0; i < files.length; ++i){
      this.uploadFile(files[i]);
    }
  }

  uploadFileName(file){
      var name = file.name;
      db.get("images").then(function(doc){
         if(!doc.names.includes(name)){
             doc.names.push(name);
         }
        db.put(doc)
      });
  }

  uploadFile(file){
    let {backgrounds} = this.state;
    let that = this;
    var name = file.name;
    var type = file.type;
    //console.log(file)

    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = function(){

      var data = reader.result;

      db.get(name).catch(function(){
        let upload = {
          _id: name
        }
        return db.put(upload);
      }).then(function(){
        return db.get(name)
      }).then(function(doc){
        db.putAttachment(name, name, doc._rev, file, type);
          let imgdata = {
            "name": name,
            "data": data
          }
          backgrounds.push(imgdata);
          that.setState({backgrounds: backgrounds})
      })


      // let img = {
      //   "name": name,
      //   "data": data
      // }
      //backgrounds.push(img);
      //update state
      //that.setState({backgrounds: backgrounds})

      //update database
      // db.get("images").then(function (doc) {
      //   doc.images = backgrounds;
      //   db.put(doc);
      // })
    }
  }

  setWordIndex(index, lyrics){

    let scrollTo = index;

    if(index > this.state.wordIndex && index+1 < this.state.item.words.length)
      scrollTo+=1;
    if(index < this.state.wordIndex && index > 0){
      scrollTo-=1;
    }

    var mElement = document.getElementById("MSlide"+scrollTo);
    var element = document.getElementById("Slide"+index);
    if(mElement)
      mElement.scrollIntoView({behavior: "smooth", block: "center", inline:'nearest'});
    if(element)
      element.scrollIntoView({behavior: "smooth", block: "center", inline:'nearest'});

    this.setState({wordIndex: index});
    let {item} = this.state;
    if(!item.style)
      return;
    let fSize;
    if(index === 0)
      fSize = item.nameSize
    else
      fSize = item.style.fontSize

    let style = {
      color: item.style.color,
      fontSize: fSize,
    }
    this.updateCurrent({words: lyrics, style: style});

  }

  updateCurrent({
    words = "unchanged",
    background = "unchanged",
    style = "unchanged"
    } = {}){

    db.get("currentInfo").then(function (doc) {
        if(words !== "unchanged")
          doc.info.words = words;
        if(background !== "unchanged")
          doc.info.background = background;
        if(style !== "unchanged")
          doc.info.style = style;
        db.put(doc);
      })
  }


  setItemIndex(index){

    var that = this;
    let {itemList} = this.state;

    var mElement = document.getElementById("MItem"+index);
  //  var element = document.getElementById("Item"+index);
    if(mElement)
      mElement.scrollIntoView({behavior: "smooth", block: "center", inline:'nearest'});
    // if(element)
      // element.scrollIntoView({behavior: "smooth", block: "end", inline:'nearest'});
    this.setState({itemIndex: index})
    if(itemList.length !== 0){
      let itemID = itemList[index]._id;
      db.get(itemID).then(function (doc) {
          that.setState({
            item: doc,
            wordIndex: 0,
          })

          let style = {
            color: doc.style.color,
            fontSize: doc.nameSize,
          }
          that.updateCurrent({words: doc.words[0], background: doc.background, style: style} );
      })
    }
  }

  sortItemList(list){
    return list.sort(function(a,b){
      var nameA = a.name.toUpperCase();
      var nameB = b.name.toUpperCase();

      if(nameA < nameB)
       return -1;
      if(nameA > nameB)
       return 1;
      return 0;
    });
  }

  updateItem(item){
    let {itemList, itemIndex, allItems} = this.state;
    itemList[itemIndex].name = item.name;

    allItems = this.sortItemList(allItems)

    let index = allItems.findIndex(e => e._id === item._id)
    allItems[index].name = item.name;

    this.setState({
      item: item,
      itemList: itemList,
      allItems: allItems,
      needsUpdate: true
    });
  }

  addItemToList(item){
    let {selectedItemList, itemIndex} = this.state;
    let that = this;
    //format item object for list
    let itemObj = {
      "name": item.name,
      "_id": item._id,
      "background": item.background,
      "nameColor": item.nameColor,
      "type": item.type
    }
    //put item in current item list
    db.get(selectedItemList).then(function (doc) {
      doc.items.splice(itemIndex+1, 0, itemObj);
      that.setState({itemList: doc.items});
      db.put(doc);
    })
    //select created item
    db.get(item._id).then(function(doc){
      console.log("Item retreived")
      that.setState({item: doc});
    });
  }

  addItem(item){
    var that = this;

    let itemObj = {
      "name": item.name,
      "_id": item._id,
      "background": item.background,
      "nameColor": item.style.color,
      "type": item.type
    }

    //add item to database
    db.put(item);

    //add Item to current list
    this.addItemToList(itemObj)

    //add Item to full list
    db.get('allItems').then(function (doc) {
      doc.items.push(itemObj);
      doc.items = that.sortItemList(doc.items)
      that.setState({allItems: doc.items})
      db.put(doc);
    });
  }

  deleteItem(name){
    let {allItems, item} = this.state;
    let that = this;

    if(name === item.name){
      this.setState({item: ""})
    }

    let index = allItems.findIndex(e => e.name === name)
    db.get(allItems[index]._id).then(function (doc) {
        return db.remove(doc);
    });
    allItems.splice(index, 1);
    db.get('allItems').then(function(doc){
      doc.items = allItems;
      return db.put(doc);
    }).then(function (){
      return db.get('allItems');
    }).then(function(doc){
      that.setState({allItems: doc.items});
    });

    for(let i = 1; i <= 5; ++i){
      db.get("Item List "+i).then(function(doc){
        let sIndex = doc.items.findIndex(e => e.name === name);
        if(sIndex >= 0){
          let {selectedItemList, itemIndex} = that.state;
          if(selectedItemList === "Item List "+i){
            let {itemList} = that.state;
            itemList.splice(sIndex, 1);
            if(sIndex === itemIndex){
              that.setWordIndex(0);
            }
            if(sIndex === itemList.length){
              that.setItemIndex(0);
            }

            db.get(selectedItemList).then(function (doc) {
                doc.items = itemList;
                return db.put(doc);
              }).then(function (){
                return db.get(selectedItemList);
              }).then(function(doc){
                that.setState({itemList: doc.items});
              });
          }
          else{
            db.get("Item List "+i).then(function (doc) {
                let itemList = doc.items;
                itemList.splice(sIndex, 1);
                doc.items = itemList;
                return db.put(doc);
              })
          }

        }
      })
    }
  }

  deleteItemFromList(index){
    var that = this;
    let {itemIndex, itemList, selectedItemList} = this.state;

    itemList.splice(index, 1);
    if(index === itemIndex){
      this.setWordIndex(0);
    }
    if(itemIndex === itemList.length){
      this.setItemIndex(0);
    }

      db.get(selectedItemList).then(function (doc) {
        doc.items = itemList;
        return db.put(doc);
      }).then(function (){
        return db.get(selectedItemList);
      }).then(function(doc){
        that.setState({itemList: doc.items});
      });
  }

  selectItemList(event){
    var id = event.target.value;
    this.setState({selectedItemList: id})
    var that = this;
    db.get(id).then(function(doc){
      that.setState({itemList: doc.items})
    })
  }

  swapItemInList(index1, index2){
    var that = this;
    let {itemList, selectedItemList} = this.state;

    let temp = itemList[index1];
    itemList[index1] = itemList[index2];
    itemList[index2] = temp;

    db.get(selectedItemList).then(function(doc){
      doc.items = itemList;
      that.setState({itemList: itemList});
      db.put(doc);
    })

  }

  swapWords(index1, index2){
    var that = this;
    let {item} = this.state;

    let temp = item.words[index1];
    item.words[index1] = item.words[index2];
    item.words[index2] = temp;

    db.get(item._id).then(function(doc){
      doc.words = item.words;
      that.setState({item: item});
      db.put(doc);
    })

  }

  test(){
    // let url = "http://anyorigin.com/go?url=" +
    // encodeURIComponent('https://www.biblegateway.com/passage/?search=Jeremiah+4&version=NKJV')
    // + "&callback=?"
    // this.setState({url:url})
    // fetch(url).then(function(response){
    //   console.log(response);
    // })
  }

  login(){
    this.setState({isLoggedIn: true})
  }
  logout(){
    this.setState({isLoggedIn: false})
  }

  render() {

    let {wordIndex, itemIndex, currentInfo, isLoggedIn, item, allItems} = this.state;

    return (
      <div style={ window.location.hash!=="#/presentation" ? {backgroundColor: '#d9e3f4', height:'99.7vh'}
      :  {backgroundColor: '#000', height:'99.7vh'}}>
         <div style={window.location.hash==="#/fullview" ? {display: 'flex'} : {display:'none'}} >
           <div style={{paddingTop: '.75%', paddingRight:'calc(5px + 1%)'}}>
             <li><Link to="/">Home</Link></li>
           </div>
           <div style={{width:'35%', paddingTop:'1.5%'}}>
             <select style={{fontSize: "calc(10px + 0.35vmax)"}} value={this.state.selectedItemList} onChange={this.selectItemList}>
               {this.state.itemLists.map((element, index) =>
                 <option key={index}> {element} </option>
               )}
             </select>
           </div>
           <div style={{paddingTop:'1.5%'}}>
              <FormatEditor isLoggedIn={isLoggedIn} item={item} updateFormat={this.updateFormat} wordIndex={wordIndex}/>
           </div>
         </div>
         <div>
            {/* Route components are rendered if the path prop matches the current URL */}
            <Switch>
              <Route exact={true} path="/" render={(props) =>
                <Home {...props} isLoggedIn={isLoggedIn} logout={this.logout} test={this.test}
                  url={this.state.url}
              />}/>
              <Route  path="/fullview" render={(props) =>
                <FullView {...props}
                  wordIndex={wordIndex} itemIndex={itemIndex}
                  setItemIndex={this.setItemIndex} setWordIndex={this.setWordIndex}
                  isLoggedIn={this.state.isLoggedIn} updateItem={this.updateItem}
                  addItem={this.addItem} itemList={this.state.itemList} item={this.state.item}
                  deleteItemFromList={this.deleteItemFromList} backgrounds={this.state.backgrounds}
                  handleFileChange = {this.handleFileChange} setItemBackground = {this.setItemBackground}
                  updateFormat={this.updateFormat} allItems={allItems} deleteItem={this.deleteItem}
                  addItemToList={this.addItemToList} swapItemInList={this.swapItemInList} swapWords={this.swapWords}
                  testIMG = {this.state.testIMG}
                />}/>
              <Route  path="/mobile" render={(props) =>
                <MobileView {...props}
                  wordIndex={wordIndex} itemIndex={itemIndex}
                  setItemIndex={this.setItemIndex} setWordIndex={this.setWordIndex}
                  isLoggedIn={this.state.isLoggedIn} updateItem={this.updateItem}
                  addItem={this.addItem} itemList={this.state.itemList} item={this.state.item}
                  deleteItemFromList={this.deleteItemFromList} backgrounds={this.state.backgrounds}
                  handleFileChange = {this.handleFileChange} setItemBackground = {this.setItemBackground}
                  updateFormat={this.updateFormat} allItems={allItems} deleteItem={this.deleteItem}
                  addItemToList={this.addItemToList} swapItemInList={this.swapItemInList} swapWords={this.swapWords}
                  itemLists={this.state.itemLists} selectedItemList={this.state.selectedItemList}
                  selectItemList={this.selectItemList}
                />}/>
              <Route path="/login" render={(props) =>
                  <Login {...props} login={this.login}
                  />}/>
              <Route path="/presentation" render={(props) =>
                  <Presentation {...props}  text={currentInfo.words} style={currentInfo.style}
                    background={currentInfo.background} backgrounds={this.state.backgrounds}
                  />}/>
            </Switch>
        </div>
      </div>
    )
  }
}

export default App;



