import React from 'react';

class Backgrounds extends React.Component{


  render() {

    let {backgrounds, isLoggedIn} = this.props;
    let BCKS = "";

    if(backgrounds){
      if(backgrounds.length > 0){
        BCKS = backgrounds.map((pic, index) => {
        return(
          <div key={index}>
            <img onClick={ (e) => this.props.setItemBackground(pic.name)} style={{height: '3vmax', width: '5.33vmax', padding: '.5vmax'}} alt={pic.name} src={pic.data}/>
          </div>
        )
      })
      }
    }


    return (
      <div>
        <div>Backgrounds</div>
        <div>{isLoggedIn && <input style={{fontSize: "calc(8px + 0.35vmax)"}} multiple type="file" onChange={ (e) => this.props.handleFileChange(e.target.files)}/>}</div>
        <div style={{display:'flex', overflowX: 'scroll', width: '35vmax'}}>{BCKS}</div>
      </div>
    )
  }

}
export default Backgrounds;



