import React from 'react';
import deleteX from './assets/deleteX.png';
import left from './assets/left-arrow.png';
import right from './assets/right-arrow.png';
import SlideInList from './SlideInList';
import LyricsBox from './LyricsBox';

class ItemSlides extends React.Component{

  constructor(){
    super();

    this.state = {
      lBoxOpen: false
    }

    this.addSlide = this.addSlide.bind(this);
    this.deleteSlide = this.deleteSlide.bind(this);
    this.wordsLeft = this.wordsLeft.bind(this);
    this.wordsRight = this.wordsRight.bind(this);
    this.openLBox = this.openLBox.bind(this);
    this.closeLBox = this.closeLBox.bind(this);
    this.autoFormatLyrics = this.autoFormatLyrics.bind(this);
  }

  openLBox(){
    this.setState({lBoxOpen: true})
  }

  closeLBox(){
    this.setState({lBoxOpen: false})
  }

  autoFormatLyrics(lyrics){
    let {item} = this.props;
    let formattedLyrics = [];
    let added = false;

    let lines = lyrics.split("\n");
    for(var i = 0; i < lines.length; ++i){
      added = false;
      if(lines[i] === "")
        continue;
      else{
        if(lines[i+2]){
          if(lines[i].length + lines[i+1].length + lines[i+2].length <= 120){
            formattedLyrics.push("\n"+lines[i]+"\n"+lines[i+1]+"\n"+lines[i+2]);
            i+=2;
            added = true;
          }
          else if(lines[i].length + lines[i+1].length + lines[i+2].length <= 156){
            formattedLyrics.push(lines[i]+"\n"+lines[i+1]+"\n"+lines[i+2]);
            added = true;
              i+=2;
          }
        }
        if(lines[i+1] && !added){
          if(lines[i].length + lines[i+1].length <= 108){
            formattedLyrics.push("\n"+lines[i]+"\n\n"+lines[i+1]);
            i+=1;
            added = true;
          }
          else {
            if(lines[i].length > 80)
              formattedLyrics.push("\n"+lines[i]);
            else
              formattedLyrics.push("\n\n"+lines[i]);
            added = true;
          }
        }
        if(!added)
          formattedLyrics.push("\n\n"+lines[i]);
      }

    }
    this.setState({lBoxOpen: false})
    item.words.push(...formattedLyrics);
    this.props.updateItem(item);

  }

  addSlide(){
    let {item, wordIndex} = this.props;
    item.words.splice(wordIndex+1, 0, " ")
    this.props.updateItem(item);
    this.props.setWordIndex(item.words.length-1)
  }

  deleteSlide(index){
    let {item} = this.props;
    item.words.splice(index, 1);
    this.props.updateItem(item);
  }

  wordsLeft(index){
    this.props.swapWords(index, index-1)
  }

  wordsRight(index){
    this.props.swapWords(index, index+1)
  }

  render() {
    let {item, isLoggedIn, backgrounds, wordIndex} = this.props;
    let type = item.type;
    let words = item.words;
    let that = this;
    let row = [];
    let fullArray = [];
    if(!words)
      return null;

    for(var i = 0; i < words.length; i+=3){
      for(var j = i; j < i+3; ++j){
        if(words[j])
          row.push(words[j]);
      }

      fullArray.push(row);
      row = [];
    }

    let ROWtest = fullArray.map((element, index) => {
      let row = element.map(function (lyrics, i){
        let leftArrowEnable = ((index + i !== 0) && (((index + i !== 1)) || (index !== 0)));
        let rightArrowEnable = ((index + i !== (fullArray.length-1) + (element.length-1)) && (index + i !== 0));
        let deleteEnable = (index+i !== 0);
        return(
          <div style={{display:'flex', width:'33%'}} key={i} id={"Slide"+(index*3+i)}>
            <div  style={((index*3+i) === wordIndex) ? {border:'0.25vmax', borderColor: '#4286f4', borderStyle:'solid',
               width:"12.24vmax", height:"6.885vmax"}
                : { border:'0.25vmax', borderColor: '#d9e3f4', borderStyle:'solid',
               width:"12.24vmax", height:"6.885vmax"}}
              onClick={() => (that.props.setWordIndex(index*3+i, lyrics))}
              >
              <SlideInList words={lyrics} background={item.background}
                color={item.style.color} fontSize={(index === 0 && i === 0) ? item.nameSize : item.style.fontSize} backgrounds={backgrounds}
                />
            </div>
            {isLoggedIn && <div style={{padding:'1.5%'}}>
              {deleteEnable && <img style={{display:'block', width:'1.5vmax', height:'1.5vmax'}}
                 onClick={() => (that.deleteSlide(index*3+i))}
                 alt="delete" src={deleteX}
                />
              }
              {leftArrowEnable && <img style={{display:'block', width:'1.5vmax', height:'1.5vmax'}}
                 onClick={() => (that.wordsLeft(index*3+i))}
                 alt="left" src={left}
                />
              }
              {rightArrowEnable && <img style={{display:'block', width:'1.5vmax', height:'1.5vmax'}}
                 onClick={() => (that.wordsRight(index*3+i))}
                 alt="right" src={right}
                />
              }
            </div>}
          </div>
        );
      })
      return (
        <div style={{display:'flex', paddingBottom:'1vmax'}} key={index}> {row}</div>
      );
    })

    return (
      <div>
        {(type === 'song') && <div>
          <div style={{display:'flex'}}>
            {isLoggedIn && <button style={{fontSize: "0.85vmax", marginRight:'0.5%'}} onClick={this.addSlide}>Add Slide</button> }
            {isLoggedIn && <button style={{fontSize: "0.85vmax"}} onClick={this.openLBox}>Format New Lyrics</button> }
          </div>
        <div style={{ overflowY: 'scroll', height: "36vh", width:"45vmax"}}>{ROWtest}</div>
        {this.state.lBoxOpen &&<LyricsBox cancel={this.closeLBox} autoFormatLyrics={this.autoFormatLyrics}/>}
      </div>}
      </div>
    )
  }

}
export default ItemSlides;



